export default {
	data() {
		return {
			orderInfo: {},
			content: ""
		}
	},
	onLoad(params) {
		let _this = this;
		_this.request({
			url: _this.http.orderDetail,
			data: {
				orderId: params.orderId
			},
			success: (res) => {
				if (res.code === '0') {
					_this.orderInfo = res.result;
				} else {
					uni.showToast({
						title: res.message,
						icon: 'none'
					});
				}
			}
		});
	},
	methods: {
		submit() {
			let _this = this;
			if (_this.content === "") {
				return uni.showToast({
					title: "请输入评价内容",
					icon: 'none'
				});
			}
			if (_this.content.length > 200) {
				return uni.showToast({
					title: "请输入200字以内的评论内容",
					icon: 'none'
				});
			}
			_this.request({
				url: _this.http.addAppraise,
				data: {
					orderId: _this.orderInfo.orderId,
					driverId: uni.getStorageSync("userInfo").driverId,
					content: _this.content
				},
				success: (res) => {
					if (res.code === '0') {
						uni.showToast({
							title: "评价成功",
							icon: 'success'
						});
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
}
